const API_BASE_URL = "http://localhost:8000"

export function getToken(): string | null {
  if (typeof window !== "undefined") {
    return localStorage.getItem("token")
  }
  return null
}

export function setToken(token: string): void {
  localStorage.setItem("token", token)
}

export function setUser(user: User): void {
  localStorage.setItem("user", JSON.stringify(user))
}

export function getUser(): User | null {
  if (typeof window !== "undefined") {
    try {
      const userStr = localStorage.getItem("user")
      console.log("getUser: Raw userStr from localStorage:", userStr)
      
      if (!userStr || userStr === "undefined" || userStr === "null") {
        console.log("getUser: No valid user data found")
        return null
      }
      
      const user = JSON.parse(userStr)
      console.log("getUser: Parsed user:", user)
      return user
    } catch (error) {
      console.error("Error parsing user from localStorage:", error)
      // Nettoyer les données corrompues
      localStorage.removeItem("user")
      return null
    }
  }
  return null
}

export function isAdmin(): boolean {
  try {
    const user = getUser()
    if (!user) {
      console.log("isAdmin: No user found")
      return false
    }
    
    // Seul l'utilisateur avec username exact "admin" est admin
    const isAdminUser = user.username === "admin"
    console.log("isAdmin check:", { username: user.username, isAdmin: isAdminUser })
    return isAdminUser
  } catch (error) {
    console.error("Error checking admin status:", error)
    return false
  }
}

export function removeToken(): void {
  if (typeof window !== "undefined") {
    localStorage.removeItem("token")
    localStorage.removeItem("user")
    localStorage.removeItem("lastRunId")
    localStorage.removeItem("lastTopic")
  }
}

export async function apiRequest<T>(endpoint: string, options: RequestInit = {}): Promise<T> {
  const token = getToken()

  const headers: HeadersInit = {
    "Content-Type": "application/json",
    ...options.headers,
  }

  if (token) {
    ;(headers as Record<string, string>)["Authorization"] = `Bearer ${token}`
  }

  const response = await fetch(`${API_BASE_URL}${endpoint}`, {
    ...options,
    headers,
  })

  if (response.status === 401) {
    removeToken()
    if (typeof window !== "undefined") {
      window.location.href = "/login"
    }
    throw new Error("Non autorisé")
  }

  if (!response.ok) {
    throw new Error(`Erreur API: ${response.status}`)
  }

  return response.json()
}

export async function login(username: string, password: string): Promise<{ token: string; user: User }> {
  const response = await fetch(`${API_BASE_URL}/auth/login`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ username, password }),
  })

  if (!response.ok) {
    throw new Error("Identifiants invalides")
  }

  const data = await response.json()
  
  // Créer un objet User compatible, même si le backend ne retourne pas de rôle
  let user: User
  
  if (data.user && data.user.role) {
    // Le backend retourne un utilisateur avec rôle
    user = data.user
  } else if (data.user) {
    // Le backend retourne un utilisateur sans rôle (votre structure actuelle)
    user = {
      id: String(data.user.id), // Convertir nombre en string
      username: data.user.username,
      role: data.user.username === "admin" ? "admin" : "user" // Seul username exact "admin" est admin
    }
  } else {
    // Fallback si aucun utilisateur n'est retourné
    user = {
      id: "temp",
      username: username,
      role: username === "admin" ? "admin" : "user" // Déterminer par username exact
    }
  }
  
  return { token: data.access_token, user }
}

export interface User {
  id: string
  username: string
  role: "admin" | "user"
  created_at?: string
}

// Interface pour votre table existante (sans rôle)
export interface ExistingUser {
  id: string | number  // Votre base retourne un nombre, on le convertit en string
  username: string
  password_hash: string
  api_token?: string
  created_at?: string
}

export interface Agent {
  id: string
  name: string
  role: string
  goal: string
  backstory: string
}

export async function getAgents(): Promise<Agent[]> {
  return apiRequest<Agent[]>("/api/agents")
}

export async function createAgent(agent: Omit<Agent, "id">): Promise<Agent> {
  return apiRequest<Agent>("/api/agents", {
    method: "POST",
    body: JSON.stringify(agent),
  })
}

export interface CrewRunResponse {
  run_id: string
  status: string
}

export async function runCrew(topic: string, agentIds: string[]): Promise<CrewRunResponse> {
  return apiRequest<CrewRunResponse>("/api/crew/run", {
    method: "POST",
    body: JSON.stringify({ topic, agent_ids: agentIds }),
  })
}

export interface CrewResult {
  status: string
  // backend returns `result_long`; keep `result` as optional alias
  result?: string
  result_long?: string
}

export async function getCrewResult(runId: string): Promise<CrewResult> {
  return apiRequest<CrewResult>(`/api/crew/result/${runId}`)
}

